import React, { Component } from 'react'
import { connect } from 'react-redux';
import { Container } from "react-bootstrap";
import MenuContainer from '../MenuContainer/MenuContainer.js';

class List extends Component {
    render () {
        const selectedMenu  =  this.props.bottom_navigation_events.filter(item => item.status == true);
        return (
            <React.Fragment>
            <Container fluid={true}>
           { selectedMenu.map(item => {
            return ( <MenuContainer class="popover " data={item}/> );
            })}
            </Container>
            </React.Fragment>
        );
     
    }
}
//bind states to props and get triggered props
const mapStateToProps = state => {
    return {
        bottom_navigation_events:  state.bottom_navigation_events
    }
}

const ListContainer =  connect(mapStateToProps)(List);
export default ListContainer