import React, { Component } from "react";
import { Card, Container, Row, Col, Image } from "react-bootstrap";
import "./FooterItem.css";

class FooterItem extends Component {
  constructor(props) {
    super(props);
    this.state = {
      active: true
    };
  }

  importAll = r => {
    let images = {};
    r.keys().map((item, index) => { images[item.replace('./', '')] = r(item); });
    return images;
  }

  render() {
    const images = this.importAll(require.context("../../Resources/Navigation/", false, /\.(png|jpe?g|svg)$/));
    let className =
      "zero-border zero-padding card-container " +
      (this.props.data.status ? "Active" : "Inactive");
    let imagePath = this.props.data.title + "-" + (this.props.data.status ? "Active" : "Inactive") + ".svg";
    return (
      <div className={className}>
        <div className="footer-container">
          <div className="footer-item footer-image"><Image src={images[imagePath]} /> </div>
          <div className="footer-item footer-title">{this.props.data.title}</div>
        </div>
      </div>
    );
  }
}

export default FooterItem;
