import {CLICK_BOTTOM_NAVIGATION_EVENT} from '../../Constants/action_types';
const initialState = {
    bottom_navigation_events : [
            {
                title: "Shape",
                id: 1,
                status : false
            },
            {
                title: "Dimensions",
                id: 2,
                status : false
            },
            {
                title: "Decking",
                id: 3,
                status : false
            },
            {
                title: "Shape",
                id: 4,
                status : false
            },
            {
                title: "Substructure",
                id: 5,
                status : false
            },
            {
                title: "Stairs",
                id: 6,
                status : false
            },
            {
                title: "Railings",
                id: 7,
                status : false
            },
            {
                title: "Furniture",
                id: 8,
                status : false
            },
            {
                title: "Photo",
                id: 9,
                status : false
            }
    ]
};
function rootReducers(state = initialState, action) {
    if(action.type === CLICK_BOTTOM_NAVIGATION_EVENT) {
        return Object.assign({}, state, {
            bottom_navigation_events: state.bottom_navigation_events.map(navigationItem => {
              if (navigationItem.id !== action.payload.id) {
                return Object.assign({}, navigationItem, {
                    status: false
                  })
              }
              return Object.assign({}, navigationItem, {
                status: !navigationItem.status
              })
            })
          })
    }
    return state;
}
export default rootReducers;