/*eslint no-console: ["error", { allow: ["warn", "error"] }] */
import React, { Component } from 'react'
import Unity, { UnityContent } from "react-unity-webgl";
import PropTypes from "prop-types";
class UnityDashboard extends Component {
    constructor(props) {
        super(props);
        this.state = ({
            progression : 0, 
            loading : true
        })
        this.unityContent = new UnityContent(
            "deck.json",
            "UnityLoader.js", {
                adjustOnWindowResize: true 
            }
          );
          this.unityContent.on("progress", progression => {
            if(progression >= 0.9) {
                this.setState({
                    progression : progression,
                    loading : false
                })
            } else {
            this.setState({
                progression : progression
            })
            }   
          });
    }
    configureUnityContent = (progress) => {
        if(progress >= 0.9) {
            this.setState({
                loading : false
            })
            return 
        } else {
            this.showProgress()
        }
    }
    handleClick = () => {
        this.unityContent.send(
            "HoleDirt", 
            "HoleRenderer", 
            10
          );
    }
    showProgress = () => {
        return <div style={{textAlign:"center", color:'white'}}>Loading ...{Math.round(this.state.progression * 100) + "%"}</div>
    }
    render () {
        //console.log(this.props);
        return (
            <div className="sub-container">
                {/* <div onClick={() => this.handleClick()}>Spawn</div> */}
                <div className="sub-container" style={{display:this.state.loading ? '' : 'none'}}>
                    {this.showProgress()}
                </div>
                <div className="sub-container" style={{display:this.state.loading ? 'none' : ''}}>
                    <Unity unityContent={this.unityContent} />
                </div>
            </div>
        )
    }
}
UnityDashboard.propTypes = {
    unityAppFlow: PropTypes.string
  };
export default UnityDashboard