import React, { Component } from "react";
import { Container, Row, Col } from "react-bootstrap";
import FooterItem from "./FooterItem";
import {connect} from 'react-redux';
import { handleBottomNavigationItem } from '../../redux/actions'
import "./FooterItem.css";
class Footer extends Component {
  constructor(props) {
    super(props);
    this.state = {
      lastClicked : 0
    }
  }
  //handling click event on footer item
  footerItemClicked = (item) => {
      if(this.state.lastClicked == item.id) return;
      this.setState({
        lastClicked : item.id
      })
      this.props.handleBottomNavigationItem(item);
  }
  render() {
    console.log(this.props.bottom_navigation_events);
    return (
      <React.Fragment>
        <Container fluid={true}>
          <Row>
            {this.props.bottom_navigation_events.map(item => {
              return (
                <Col className="zero-padding" onClick={()=>this.footerItemClicked(item)}>
                  <FooterItem data={item} key={item.id} />
                </Col>
              );
            })}
          </Row>
        </Container>
      </React.Fragment>
    );
  }
}
function mapDispatchToProps(dispatch) {
  return {
      handleBottomNavigationItem: (article) => dispatch(handleBottomNavigationItem(article))
  };
}
const mapStateToProps = state => {
  return {
      bottom_navigation_events: state.bottom_navigation_events
  }
}
const FooterList =  connect(mapStateToProps, mapDispatchToProps)(Footer);
export default FooterList;
